import React from 'react';
import './Visualizer.css';
import Slider from 'react-input-slider';
import Sketch from 'react-p5';
import PopUp from "./PopUp"; 

export default class SortingVisualizer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            w: 30,
            sortSpeed: 200,
            values: [],
            states: [],
            seen: false
        };
    };

    togglePop = () => {
        this.setState({
            seen: !this.state.seen
        });
    };

    async bubbleSort(arr) {
        let i = 0;
            for(let j = 0; j < arr.length - i - 1; j ++) {
                let a = arr[j];
                let b = arr[j + 1];
                if(a > b) {
                    this.swap(arr, j, j + 1);
                }
            }
    }
    
    insertionSort() {

    };

    selectionSort() {

    };

    async quickSort(arr, start, end) {
        this.setState({
            draw: true
        })
        if (start >= end) {
            return;
        }
        let index = await this.partition(arr, start, end);
        this.state.states[index] = -1;

        await Promise.all([
            this.quickSort(arr, start, index - 1),
            this.quickSort(arr, index + 1, end)
        ]);
    };

    async partition(arr, start, end) {
        for(let i = start; i < end; i ++) {
            this.state.states[i] = 1;
        }

        let pivotValue = arr[end];
        let pivotIndex = start;
        this.state.states[pivotIndex] = 0;

        for(let i = start; i < end; i ++) {
            if(arr[i] < pivotValue) {
                await this.swap(arr, i, pivotIndex);
                this.state.states[pivotIndex] = -1;
                pivotIndex ++;
                this.state.states[pivotIndex] = 0;
            }
        }

        await this.swap(arr, pivotIndex, end);

        for(let i = start; i < end; i ++) {
            if (i !== pivotIndex) {
                this.state.states[i] = -1;
            }
        }

        return pivotIndex;
    };

    mergeSort() {
        
    };

    heapSort() {

    };

    trumpSort() {

    };

    async bernieSort(arr) {
        //replace all values with the mean
        var total = 0;
        for(var i = 0; i < arr.length; i++) {
            total += arr[i];
        }
        var avg = total / arr.length;

        for(i = 0; i < arr.length; i++) {
            await this.sleep(15);
            arr[i] = avg;
        }
    };

    async thanosSort(arr) {
        for (var i = 0; i < arr.length; i++) {
            await this.sleep(15);
            arr.splice(i + 1, 1);
        }
        this.quickSort(this.state.values, 0, this.state.values.length - 1);
    };

    //Description: Deletes all the elements that are not in order
    async stalinSort(arr) {
        alert("Stalin Sort deletes all the elements that are not already in order ðŸ‡·ðŸ‡º")
        const temp = arr.slice();
        temp.sort();
        for (var i = 0; i < arr.length; i++) {
            if(arr[i] !== temp[i]) {
                await this.sleep(25);
                arr.splice(i, 1);
                i --;
            }
        }
    };

    async buddhaSort(arr) {
        alert("The array, like all things, is ephemeral, thus its order is insignificant. Leave the array as it is, and pursue enlightment instead.");
        //  that the array, like all things, is ephemeral and its order is insignificant so you just leave it like that and pursue enlightenment instead.
    };

    async swap(arr, a, b) {
        await this.sleep(251 - this.state.sortSpeed);
        let temp = arr[a];
        arr[a] = arr[b];
        arr[b] = temp;
    };

    sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    };

    newArray() {
        const array = [];

        for(let i = 0; i < Math.floor(window.innerWidth / this.state.w); i ++) {
            array.push(randomInt(10, window.innerHeight - 158));
        }
        this.setState({ values: array });
    }; 

    render() {
        return (
            <div className="App" style = {{ backgroundColor: 'lightyellow', height: '100% !important'}}>
				<Sketch
                    setup={(p5, parentRef) => {
                        p5.createCanvas(window.innerWidth, window.innerHeight - 158);
                        this.state.values = new Array(Math.floor(p5.width / this.state.w));
                        this.newArray();
					}}
                    
                    draw = {(p5) => {
                        p5.clear();
                        p5.background('lightyellow');
                        for (let i = 0; i < this.state.values.length; i ++) {
                            p5.fill('pink');
                            p5.stroke('lightyellow');
                            p5.rect(i * this.state.w, p5.height - this.state.values[i], this.state.w, this.state.values[i], 20, 20, 0, 0);
                        }
                    
                        
                        // if(this.state.draw) {
                        //     for (let i = 0; i < this.state.values.length; i ++) {
                        //         if (this.state.states[i] === 0) {
                        //             p5.fill('#E0777D');
                        //         } else if (this.state.states[i] === 1) {
                        //             p5.fill('#D6FFB7');
                        //         } else {
                        //             p5.fill(255);
                        //         }
                        //         p5.rect(i * this.state.w, p5.height - this.state.values[i], this.state.w, this.state.values[i]);
                        //     }
                        // } else {
                        //     for (let i = 0; i < this.state.values.length; i ++) {
                        //         p5.rect(i * this.state.w, p5.height - this.state.values[i], this.state.w, this.state.values[i]);
                        //     }
                        // }
                        // this.setState({
                        //     draw: false
                        // })
                    }}
				/>

                <br />
                <span style={{marginRight: 25, marginTop: 20}}>Size</span>
                <Slider x={this.state.w} xmin={5} xmax={window.innerWidth / 4} xstep={1} onChange={({x}) => this.setState({ w: x })} onDragEnd={() => this.newArray()} />
                <input type="text" value={Math.floor(window.innerWidth / this.state.w)} style= {{ width: 22, marginLeft: 15}} disabled={true}/>
                
                <br/>
                <span style={{marginRight: 25}}>Speed</span>
                <Slider x={this.state.sortSpeed} xmin={1} xmax={250} xstep={1} onChange={({x}) => this.setState({ sortSpeed: x })}/>
                <input type="text" value={this.state.sortSpeed} style= {{ width: 22, marginLeft: 15, marginBottom: 10}} disabled={true}/>

                <br />
                <button onClick={() =>  this.newArray(this.state.values, 0, this.state.values.length - 1)}>New Array</button>

                <br /><br /><br />
                <button onClick={() =>  this.bubbleSort(this.state.values)} style={{ margin: 5 }}>Bubble Sort</button>
                <button onClick={() =>  this.insertionSort(this.state.values, 0, this.state.values.length - 1)} style={{ margin: 5 }}>Insertion Sort</button>
                <button onClick={() =>  this.selectionSort(this.state.values, 0, this.state.values.length - 1)} style={{ margin: 5 }}>Selection Sort</button>
                <button onClick={() =>  this.quickSort(this.state.values, 0, this.state.values.length - 1)} style={{ margin: 5 }}>Quick Sort</button>
                <button onClick={() =>  this.mergeSort(this.state.values, 0, this.state.values.length - 1)} style={{ margin: 5 }}>Merge Sort</button>
                <button onClick={() =>  this.heapSort(this.state.values, 0, this.state.values.length - 1)} style={{ margin: 5 }}>Heap Sort</button>
                <button onClick={() =>  this.thanosSort(this.state.values)} style={{ margin: 5 }}>Thanos Sort</button>
                <button onClick={() =>  this.togglePop()} style={{ margin: 5 }}>Trump Sort</button>
                {this.state.seen ? <PopUp toggle={this.togglePop} /> : null}
                <button onClick={() =>  this.bernieSort(this.state.values)} style={{ margin: 5 }}>Bernie Sort</button>
                <button onClick={() =>  this.stalinSort(this.state.values)} style={{ margin: 5 }}>Stalin Sort</button>
                <button onClick={() =>  this.buddhaSort(this.state.values)} style={{ margin: 5 }}>Buddha Sort</button>
			</div>
        );
    }
}

function randomInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min);
}